<template>
<div class="width:100%;height:100%;background: rgb(238, 255, 247);">
<el-menu
:default-active="activeIndex"
  v-if="activeIndex"
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  background-color="#545c64"
  text-color="#fff"
  active-text-color="#ffd04b"
  border-radius="8px"
  >
  <el-menu-item index="1" >OverView</el-menu-item>
  <el-submenu index="2">
    <template slot="title">+</template>
    <el-menu-item index="2-1"></el-menu-item>
    <el-menu-item index="2-2"></el-menu-item>
  </el-submenu>
   <el-submenu index="3" >
     <template slot="title"></template>
    <el-menu-item index="3-1"></el-menu-item>
    <el-menu-item index="3-2"></el-menu-item>
   </el-submenu>
   <el-menu-item index="4"></el-menu-item>
</el-menu>
  <div id="maincontain" style="width:100%;height:100%;background: #e5e9f2;">
<keep-alive>
    <OVERVIEW v-if="index===1" ></OVERVIEW>
    <INJECTION v-if="index===2"></INJECTION>
     <INJECTIONCALIB v-if="index===3"></INJECTIONCALIB>
    <MOTOR v-else-if="index===4" ></MOTOR>
    <MOTORMORE v-else-if="index===5"></MOTORMORE>
    <TEST v-else-if="index===6"></TEST>
    <OVERVIEW v-else></OVERVIEW>  
  </keep-alive>
</div>
</div>
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import MOTORBASE from '@/view/debugger/motor'
import MOREMOTOR from '@/view/debugger/motormore'
import OverView from '@/view/debugger/OverView'
import InjectionCalib from '@/view/debugger/InjectionCalib'
import Injection from '@/view/debugger/injection'
import Test from '@/view/debugger/Test'

export default {
  data() {
    return {
      list1: [],
      stompClient: null,
      activeIndex: '1',
       activeIndex2: '1',
      index:0,
    }
  },
   components:{
         OVERVIEW:OverView,
         INJECTION:Injection,
         INJECTIONCALIB:InjectionCalib,
         MOTOR:MOTORBASE,
         MOTORMORE:MOREMOTOR,
         TEST:Test
   },
  mounted() {
    debugger
   this.stompClient= this.$store.state.stompClient
   this.activated()
  },
  methods: {
    activated() {
    this.activeIndex = '1' // 
    },
 handleSelect(key, keyPath) {
   var _this=this
       var indexkey=0;
        console.log(key, keyPath);
        if(key=="1")indexkey=1;
        if(key=="2-1")indexkey=2;
         if(key=="2-2")indexkey=3;
        if(key=="3-1")indexkey=4;
        if(key=="3-2")indexkey=5;
         if(key=="4")indexkey=6;
        this.index =indexkey;
        if(keyPath!='#'){
      // var pageL= localStorage.getItem('page')
      // var pages=pageL.split("_")
        var page='#-#-'+this.index+'-#'
            this.$store.state.stompClient.send('/app/page/'+page,{})
       }
      }
  },
   computed: {
    secondPage(){
         debugger
        return this.$store.state.secondPage
       }, 
       stompinit(){
       return this.$store.state.stompClient
    } 
  },
  watch:{
     stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
    secondPage(newVal,oldVal){
      debugger
        console.log("newVal="+newVal)
         console.log("oldVal="+oldVal)
             if(newVal!==oldVal){
          var pagesindexs=newVal.split("-")
           if(pagesindexs[2]==1)this.activeIndex='1'
           if(pagesindexs[2]==2)this.activeIndex='2-1'
           if(pagesindexs[2]==3)this.activeIndex='2-2'
           if(pagesindexs[2]==4)this.activeIndex='3-1'
           if(pagesindexs[2]==5)this.activeIndex='3-2'
           if(pagesindexs[2]==6)this.activeIndex='4'
            this.handleSelect(this.activeIndex,'#')
           this.$store.state.thirdPage=newVal
          }
    },
  },
}

</script>


