<template>
  <div class=""  style="background: rgb(238, 255, 247);"> 
       <div class="motorhead" >
           <MotorList height="100%" width="100%"/>
        </div>
         <div class="motorContent" >
         <div class="containerContent" title="" style="width:auto;height:auto;">
             <table>
               <tr>
                 <td>
                     <div class="container" title="" style="width:390px;height:auto;">
                         <el-form :model="ruleForm"  ref="ruleForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                               <table>
                                 <tr>
                                   <td>
                                       <el-form-item label="" prop="coord" >
                                          <el-input v-model="ruleForm.coord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="readCoordLimit(ruleForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                                 <tr>
                                   <td>
                                       <el-form-item label="" prop="physicsCoord" >
                                          <el-input v-model="ruleForm.physicsCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="writeCoordLimit(ruleForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                               </table>
                         </el-form>
                    </div>
                 </td>
                  <td>
                     <div class="container" title="" style="width:390px;height:auto;">
                        <el-form :model="outStepForm"  ref="outStepForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                             <table>
                                 <tr>
                                   <td>
                                       <el-form-item label="" prop="coord" >
                                          <el-input v-model="outStepForm.forward"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="readOutMove(outStepForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                                 <tr>
                                   <td>
                                       <el-form-item label="" prop="coord" >
                                          <el-input v-model="outStepForm.reverse"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="writeOutMove(outStepForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                               </table>
                        </el-form>
                    </div>
                  </td>
               </tr>
                <tr>
                 <td>
                      <div class="container" title="" style="width:390px;height:auto;">
                      <el-form :model="zeroForm"  ref="zeroForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                             <table>
                                 <tr>
                                   <td>
                                       <el-form-item label="" prop="coord" >
                                          <el-input v-model="zeroForm.zeroCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="readZeroOpt(zeroForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                                 <tr>
                                   <td>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="writeZeroOpt(zeroForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                               </table>
                        </el-form>
                    </div>
                 </td>
                  <td>
                       <div class="container" title="" style="width:390px;height:auto;">
                       <el-form :model="fileForm"  ref="zeroForm" label-width="0px" class="demo-ruleForm" style="margin-top: 20px;">
                             <table>
                                 <tr>
                                    <td class="tdpad">
                                     <el-row>
                                        <el-button type="primary"  @click="createMotorFile()" plain></el-button>
                                      </el-row>
                                   </td>
                                   <td>

               <div>
    <input type="file" @change="inputFileChange"> 
    <el-button type="primary" size="mini" @click="clicks"></el-button>
    <div id='dd' hidden></div>
  </div>
                                   </td>
                                  
                                   <td class="tdpad">
                                   </td>
                                 </tr>
                               </table>
                        </el-form>
                    </div>
                  </td>
               </tr>
                <tr>
                 <td colspan="2">
                      <div class="container" title="" style="width:550px;height:auto;">
                        <el-form :model="speedForm" ref="speedForm" label-width="273px" class="demo-ruleForm" style="margin-top: 20px;">
                             <table class="spacc">
                                 <tr>
                                   <td>
                                       <el-form-item label="(p/sec, 1=3200p)" prop="coord" >
                                          <el-input v-model="speedForm.speedCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="readSpeedAcc(speedForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                                    <tr>
                                   <td>
                                       <el-form-item label="(p/sec^2, 1=3200p)" prop="coord" >
                                          <el-input v-model="speedForm.accCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td class="tdpad">
                                      <el-row>
                                        <el-button type="primary"  @click="writeSpeedAcc(speedForm)" plain></el-button>
                                      </el-row>
                                   </td>
                                 </tr>
                                  <tr>
                                   <td>
                                       <el-form-item label="(p/sec, 1=3200p)" prop="coord" >
                                          <el-input v-model="speedForm.resetCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td>
                                   </td>
                                 </tr>
                                 <tr>
                                   <td>
                                       <el-form-item label="(p/sec^2, 1=3200p)" prop="coord" >
                                          <el-input v-model="speedForm.resetAccCoord"></el-input>
                                       </el-form-item>
                                   </td>
                                   <td>
                                   </td>
                                 </tr>
                               </table>
                        </el-form>
                    </div>
                 </td>
               </tr>
               
             </table>
         </div>
          </div>
</div> 
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import MotorList from './motorList' 
import Bus from '../../components/bus' //
export default {
  data() {
    return {
      list1: [],
      stompClient: null,
       motorId:0,
       fileList: [],
       localFile:{},
       attachmentUrl: '',
       files:'',
       ruleForm: {
          coord: '',
          physicsCoord: ''
        },
        outStepForm:{
          reverse: '',
          forward: ''
        },
        zeroForm:{
          zeroCoord: ''
        },
        speedForm:{
           speedCoord: '',
           accCoord: '',
           resetCoord: '',
           resetAccCoord: '',
        },
        fileForm:{
           zeroCoord: ''
        },
        addFileName:'',
        addType:'',
        addId:'',
        addArr:[],
        baseread:''
    }
  },
   components: {
    MotorList
    },
  mounted() {
 this.stompClient= this.$store.state.stompClient;
   // this.loadselect();//
   this.motorId=this.$store.state.motorId;
 var _this = this;// ``
    Bus.$on('phymax',function(val){//firsttxt
           var op= val.split(';')
            _this.ruleForm.physicsCoord=op[0]
            _this.ruleForm.coord=op[1]
        })
         Bus.$on('outMove',function(val){//firsttxt
           var op= val.split(';')
            _this.outStepForm.reverse=op[0]
            _this.outStepForm.forward=op[1]
        })
     Bus.$on('zeroOpt',function(val){//firsttxt
            _this.zeroForm.zeroCoord=val
        })
        //  Bus.$on('motorIdId',function(val){//firsttxt
        //     _this.motorId=val
        // })
        
       Bus.$on('speedAccList',function(val){//firsttxt
         
           var op= val.split(';')
              _this.speedForm.speedCoord=op[0]
             _this.speedForm.accCoord=op[1]
              _this.speedForm.resetCoord=op[2]
              _this.speedForm.resetAccCoord=op[3]
        })
  },
  methods: {
     checkNum:function(val) {
             var reg = /^[+-]?\d*\.?\d{1,3}$/;
              if (!reg.test(val)) {
                return false;
               }
            },
    readCoordLimit(ruleForm){
        Bus.$emit('progres',true) 
      this.$store.state.resinfo=""
       var messageJson  = JSON.stringify({
            motorId: this.$store.state.motorId
          });
       this.stompClient.send('/app/coordLimit/read',messageJson)
       
    },
    writeCoordLimit(ruleForm){
      if(this.checkNum(ruleForm.coord)==false){
            alert("")
                return;
      }
      if(this.checkNum(ruleForm.physicsCoord)==false){
            alert("")
                return;
      }
        Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var messageJson  = JSON.stringify({
            coord:ruleForm.coord,
            acce:ruleForm.physicsCoord,
            motorId:this.$store.state.motorId
          });
       this.stompClient.send('/app/coordLimit/write',messageJson)
    },
    readOutMove(outStepForm){
        Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var messageJson = JSON.stringify({
            motorId: this.$store.state.motorId
          });
       this.stompClient.send('/app/outStepForm/read',messageJson,{})
       
    },
     writeOutMove(outStepForm){
       if(this.checkNum(outStepForm.forward)==false){
            alert("")
                return;
      }
       if(this.checkNum(outStepForm.reverse)==false){
            alert("")
                return;
      }
         Bus.$emit('progres',true) 
      this.$store.state.resinfo=""
       var messageJson =""
         messageJson = JSON.stringify({
            motorId: this.$store.state.motorId,
           coord:outStepForm.forward,
            acce:outStepForm.reverse
          });
    this.stompClient.send('/app/outStepForm/write',messageJson,{})
    },
    readZeroOpt(zeroForm){
        Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var messageJson  = JSON.stringify({
            motorId: this.$store.state.motorId
          });
    this.stompClient.send('/app/zeroOpt/read',messageJson,{})
       
    },
     writeZeroOpt(zeroForm){
        if(this.checkNum(zeroForm.zeroCoord)==false){
            alert("")
                return;
      }

         Bus.$emit('progres',true) 
        this.$store.state.resinfo=""
       var messageJson = JSON.stringify({
            motorId: this.$store.state.motorId,
           coord:zeroForm.zeroCoord,
          });
      this.stompClient.send('/app/zeroOpt/write',messageJson,{})
    },
    createMotorFile(){
        Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var messageJson= JSON.stringify({
            motorId: this.$store.state.motorId
          });
    this.stompClient.send('/app/createMotorFile',messageJson,{})
    },
   readSpeedAcc(speedForm){
       Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var messageJson  = JSON.stringify({
            motorId: this.$store.state.motorId
          });
    this.stompClient.send('/app/speedAcc/read',messageJson,{})
    },
     validate:function(num){
      var reg = /^\d+(?=\.{0,1}\d+$|$)/
     if(reg.test(num)) return true;
     return false ;  
     },
     writeSpeedAcc(speedForm){
       if(this.validate(speedForm.accCoord)==false){
              alert("")
                return;
       }
       if(this.validate(speedForm.speedCoord)==false){
              alert("")
                return;
       }
       if(this.validate(speedForm.resetCoord)==false){
              alert("")
                return;
       }
       if(this.validate(speedForm.resetAccCoord)==false){
              alert("")
                return;
       }
         Bus.$emit('progres',true) 
        this.$store.state.resinfo=""
       var messageJson = JSON.stringify({
            motorId: this.$store.state.motorId,
            acce:speedForm.accCoord,
            speed:speedForm.speedCoord,
            resetSpeed:speedForm.resetCoord,
            resetAcc:speedForm.resetAccCoord
          });
    this.stompClient.send('/app/speedAcc/write',messageJson,{})
    },
    
    inputFileChange(e) {
      
      this.files = e.target.files[0]
       var imgName = this.files.name;
         var idx = imgName.lastIndexOf(".");  
          if (idx != -1){
                    var ext = imgName.substr(idx+1).toUpperCase();   
                    ext = ext.toLowerCase( ); 
                     if (ext!='json'){
                       
                    }else{
                          this.addArr.push(this.files);
                    }   
                }else{

                }
      this.reads(this.files);
    },
 reads(fil){
    var reader = new FileReader()
     reader.readAsText(fil)
     var text=''
    reader.onload = function()
    {
        
        text=reader.result.split('}')[0]+"}"
        document.getElementById("dd").innerHTML += "<input id='upfile' value='"+text+"'>"
    }
      // if (fil.length > 1) {
      //                this.form.fileList = [fileList[fileList.length - 1]]  //  json
      //             }
    //this.baseread=text
  },
    clicks() {
      if(0 == this.addArr.length){
        this.$message({
          type: 'info',
               message: ''
             });
             return;
           }
      this.$store.state.resinfo=""
      if(this.files){
        console.log('baseread='+document.getElementById("upfile").defaultValue.split('}')[0]+"}")
         var messageJson= JSON.stringify({
            file:document.getElementById("upfile").defaultValue,
            fileName:this.files.name
          });
        this.stompClient.send('/app/upload',messageJson,{})
      }
    },
    getFile(event){
      
           var file = event.target.files;
           for(var i = 0;i<file.length;i++){
            //    
               var imgName = file[i].name;
                var idx = imgName.lastIndexOf(".");  
                if (idx != -1){
                    var ext = imgName.substr(idx+1).toUpperCase();   
                    ext = ext.toLowerCase( ); 
                     if (ext!='json'){
                       
                    }else{
                          this.addArr.push(file[i]);
                    }   
                }else{

                }
           }
       },
       submitAddFile(){
         ;
           if(0 == this.addArr.length){
             this.$message({
               type: 'info',
               message: ''
             });
             return;
           }

            var formData = new FormData();
            formData.append('num', this.addType);
            formData.append('linkId',this.addId);
            formData.append('rfilename',this.addFileName);
            for(var i=0;i<this.addArr.length;i++){
                formData.append('fileUpload',this.addArr[i]);
            }
          let config = {
            headers: {
              'Content-Type': 'multipart/form-data',
              'Authorization': this.token
            }
          };
          this.axios.post(apidate.uploadEnclosure,formData,config)
            .then((response) => {
                if(response.data.info=="success"){this.$message({
                        type: 'success',
                        message: '!'
                    });
                }
            })
        }
  },
  computed: {
    stompinit(){
       return this.$store.state.stompClient
    },
  },
  watch:{
    stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
  },
}

</script>
<style rel="stylesheet/scss" lang="scss">
  .motorhead{
width:100%;
height:auto;
min-height: 12px;
margin-left: 30px;
margin-top: 10px;
  }
    .motorContent{
width:100%;
height:auto;
margin-top: 10px;

  }
    .container{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 20px;
       
    }
    .container::before{
        content:attr(title);
        position:absolute;
        left:100px;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
    .containerContent{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 20px;
       
    }
    .containerContent::before{
        content:attr(title);
        position:absolute;
        left:100px;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
//     .tdpad{
// padding-top: 20px;
// }
    // .spacc{
// .el-form-item{
//       .el-form-item__label{
//            width: 246px !important;//100
//       }
//     }
//     } 
    
</style>


