<template>
  <div class="" > 
            <el-dropdown @command="handleCommand" style="float:left">
                <span class="el-dropdown-link">
                    {{motorName}}<i class="el-icon-arrow-down el-icon--right"> </i>
                </span>
                <el-dropdown-menu slot="dropdown" >
                    <el-dropdown-item v-for="(ce,index) in kuList" :key="index" :command="ce" icon="el-icon-plus">{{ce}}</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
      </div>
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import Bus from '../../components/bus' //
export default {
  data() {
    return {
      list1: [],
      stompClient: null,
      kuList:null,
      motorId:0,
      motorName:''
    }
  },
  mounted() {
    this.stompClient= this.$store.state.stompClient;
    this.loadselect();//
    this.motorId=this.$store.state.motorId;
    if(this.kuList!=null){
       this.motorName=this.kuList[this.motorId]
    }
    var _this = this;// ``
       Bus.$on('allmotor',function(val){//firsttxt
          _this.kuList=val.split(",");
          if(_this.motorId==0){
            _this.motorName=_this.kuList[0]
          }else{
             _this.motorName= _this.kuList[_this.motorId]
          }
    });
    // Bus.$on('motorIdId',function(val){//firsttxt
    //         _this.motorId=val
    //     })
  },
  methods: {
    loadselect(){
       this.$store.state.resinfo=""
       console.log('loadMotor')
       this.stompClient.send('/app/loadMotor')
    },
    handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
   handleCommand(command) {
        var _this = this;// ``
        this.motorName=command
       var motorId= command.split("_")[1]
       this.motorId=motorId
         this.$forceUpdate()
         this.$store.state.motorId=this.motorId
         // Bus.$emit('motorIdId',this.motorId)
         this.$store.state.resinfo=""+this.motorId+""
      }
  },
  computed: {
    stompinit(){
       return this.$store.state.stompClient
    },
    motorchange(){
      return this.$store.state.motorId
    }
  },
  watch:{
    stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
    motorchange(newVal,oldVal){
      this.motorId=newVal
       this.motorName=this.kuList[newVal]
    },
  },
}
</script>
<style rel="stylesheet/scss" lang="scss">
.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .motorhead{
width:100%;
height:auto;
min-height: 12px;
margin-left: 30px;
margin-top: 10px;
  }
</style>


