<template >
<div class="" style="background: rgb(238, 255, 247);" > 
  <div class="" style="background: rgb(238, 255, 247);width:1080px;height:730px;" > 
       <div class="motorhead" >
           <MotorList height="100%" width="100%"/>
        </div>
      <div class="motorContent" >    
          <div class="container" title="" style="width:475px;height:auto; float:left;">
                  <div class="container" title="" style="width:auto;height:auto;">
                 <el-form :model="ruleForm"  ref="ruleForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                   <table>
                      <tr  >
                        <td>
                                <el-form-item label="" prop="coord" :rules="[{ type: 'number', message: ''}]">
                                      <el-input v-model="ruleForm.coord" ></el-input>
                                 </el-form-item>
                        </td>
                        <td class="tdpad">
                                <el-row>
                                      <el-button type="primary"  @click="motorcontrol(ruleForm,'backZero')" plain></el-button>
                                   </el-row>
                        </td>
                         <td class="tdpad">
                                    <el-row>
                                       <el-button type="primary"  @click="motorcontrol(ruleForm,'farZero')" plain></el-button>
                                   </el-row>
                        </td>
                       </tr>
                       <tr>
                           <td> 
                              <el-form-item label="" prop="speed" :rules="[{ required: true, message: ''},{ type: 'number', message: ''}]">
                                       <el-input v-model="ruleForm.speed"></el-input>
                                 </el-form-item>
                            </td>
                            <td class="tdpad">
                               <el-row>
                                   <el-button type="primary" @click="motorcontrol(ruleForm,'moveGo')" plain>&nbsp;&nbsp;GO&nbsp;&nbsp;</el-button>
                              </el-row>
                            </td>
                             <td class="tdpad">
                               <el-row>
                              <el-button type="primary" @click="motorcontrol(ruleForm,'seekZero')" plain>Home</el-button>
                                  </el-row>
                            </td>
                       </tr>
                        <tr>
                           <td> 
                                 <el-form-item label="" prop="acce" :rules="[{ required: true, message: ''},{ type: 'number', message: ''}]">
                                       <el-input v-model="ruleForm.acce"></el-input>
                                 </el-form-item>
                            </td>
                            <td colspan="2" class="tdpad">
                               <el-row>
                                  <el-button type="primary" @click="motorcontrol(ruleForm,'reset')" plain>&thinsp; &nbsp;&nbsp;&nbsp;&nbsp;Reset&thinsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                              </el-row>
                            </td>
                       </tr>
                        <tr>
                           <td> 
                                 <el-form-item label="" >
                                         <el-switch v-model="ruleForm.series"></el-switch>
                                 </el-form-item>
                            </td>
                            <td colspan="2">
                               <el-row class="tdpad">
                                  <el-button type="primary" @click="motorcontrol(ruleForm,'quickReset')" plain>QuickReset</el-button>
                              </el-row>
                            </td>
                       </tr>
                   </table>
                 </el-form>
                 </div> 
                   <div class="container" title="" style="width:auto;height:auto;">
                        <el-form :model="sensorForm"  ref="sensorForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                            <table>
                              <tr>
                                <td>
                                    <el-form-item label="Id" prop="sensorId" >
                                      <el-input-number v-model="sensorForm.sensorId"   :min="1" :max="10" label=""></el-input-number>
                                    </el-form-item>
                                </td>
                                <td class="tdpad">
                                    <el-row>
                                      <el-button type="primary" @click="sensorStop(sensorForm,'moveSensorStop')" plain>MoveSensorStop</el-button>
                                   </el-row>
                                </td>
                              </tr>
                               <tr>
                                <td>
                                  <el-form-item label="" prop="coord" :rules="[{ required: true, message: ''},{ type: 'number', message: ''}]">
                                      <el-input v-model="sensorForm.coord" style="width:180px"></el-input>
                                 </el-form-item>
                                </td>
                                <td class="tdpad">
                                    <el-row>
                                      <el-button type="primary" @click="sensorStop(sensorForm,'stepSensorStop')" plain>StepSensorStop</el-button>
                                   </el-row>
                                </td>
                              </tr>
                            </table>
                         </el-form>
                  </div> 
                   <div class="container" title="" style="width:auto;height:auto;">
                     <el-form :model="grooveForm"  ref="grooveForm" label-width="100px" class="demo-ruleForm" style="margin-top: 20px;">
                            <table>
                              <tr>
                                <td>
                                    <el-form-item label="Id" prop="sensorId" >
                                      <el-input-number v-model="grooveForm.sensorId"  :min="1" :max="10" label=""></el-input-number>
                                    </el-form-item>
                                </td>
                                <td class="tdpad">
                                    <el-row>
                                      <el-button type="primary" @click="grooveMove(grooveForm)" plain>Move</el-button>
                                   </el-row>
                                </td>
                              </tr>
                               <tr>
                                <td>
                                  <el-form-item label="" prop="coord" :rules="[{ required: true, message: ''},{ type: 'number', message: ''}]">
                                      <el-input v-model="grooveForm.coord" style="width:180px"></el-input>
                                 </el-form-item>
                                </td>
                                <td>
                                </td>
                              </tr>
                            </table>
                     </el-form>
                  </div> 
          </div>  
          <div class="container" title="" style="width:425px;height:auto;float:left;margin-left: 10px;">
                 <div  class="container"  title="" style="margin-top: 18px;height:60px;">
                     <div style="margin-top: 32px;">
                          <el-row style="float:left;margin-left: 10px;">
                      <el-button type="primary" @click="getCoord()"  plain></el-button>
                      </el-row>
                      <el-row  style="float:left;margin-left: 10px;">
                        <el-button type="primary" @click="steplost()" plain></el-button>
                      </el-row>
                      <el-row  style="float:left;margin-left: 10px;">
                      <el-button type="primary" @click="motorFlag()" plain></el-button>
                     </el-row>
                   </div>
                 </div>
                 <div class="container"  title="" style="height:auto;"> 
                      <el-form :inline="true" :model="formInline" class="demo-form-inline" style="margin-top: 10px;text-align: right;">
                           <el-form-item label="">
                              <!--  <el-select v-model="formInline.cmshift" placeholder="NOCHANGE">
                                <el-option label="STEP_1" value="STEP_1"></el-option>
                                  <el-option label="STEP_2" value="STEP_2"></el-option>
                                  <el-option label="STEP_4" value="STEP_4"></el-option>
                                 <el-option label="STEP_8" value="STEP_8"></el-option>
                                  <el-option label="STEP_16" value="STEP_16"></el-option>
                                  <el-option label="NOCHANGE" value="NOCHANGE"></el-option> 
                              </el-select>-->
                              <el-select v-model="formInline.cmshift" placeholder="NOCHANGE">
                                  <el-option v-for="item in cmshifts" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmstopEna" placeholder="NOCHANGE">
                                 <!-- <el-option label="DIS" value="DIS"></el-option>
                                  <el-option label="ENA" value="ENA"></el-option>
                                  <el-option label="NOCHANGE" value="NOCHANGE"></el-option> -->
                                  <el-option v-for="item in disEna" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                            <el-form-item label="">
                              <el-select v-model="formInline.cmemmrEna" placeholder="NOCHANGE">
                                 <el-option v-for="item in disEna" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>

                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmemmrtrigger" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmRotate" placeholder="NOCHANGE">
                                <el-option v-for="item in noYes" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                              <el-form-item label="">
                              <el-select v-model="formInline.cmZeroSwitch" placeholder="NOCHANGE">
                                 <el-option v-for="item in disEna" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                            <el-form-item label="">
                              <el-select v-model="formInline.cmZeroEna" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmZeroTrigger" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmMotorEna" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                            <el-form-item label="">
                              <el-select v-model="formInline.cmMotorCurr" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmMotorClk" placeholder="NOCHANGE">
                                <el-option v-for="item in fallingRising" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                             <el-form-item label="">
                              <el-select v-model="formInline.cmMotorForward" placeholder="NOCHANGE">
                                 <el-option v-for="item in lowHeight" :key="item.value" :label="item.label" :value="item.value">
                                  </el-option>
                              </el-select>
                            </el-form-item>
                            
                             <el-form-item>
                                <el-button type="primary" @click="writeInlineopt(formInline)"></el-button>
                                 <el-button type="primary" @click="searchInlineopt(formInline)"></el-button>
                             </el-form-item>
                      </el-form>
                 </div>
          </div>   
      </div>
 </div>      
</div> 
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import Bus from '../../components/bus' //
import MotorList from './motorList' 
export default {
  data() {
    return {
      list1: [],
      stompClient: null,
      kuList:null,
      motorId:0,
      ruleForm: {
          coord: 1000,
          speed: 3000,
          acce: 5000,
          series: false
        },
        sensorForm:{
          sensorId: '',
           coord: 1000,
        },
        grooveForm:{
          sensorId: '',
          coord: 1000,
        },
       formInline: {
          cmshift: '',
          cmstopEna: '',
          cmemmrEna: '',
          cmemmrtrigger: '',
          cmRotate: '',
          cmZeroSwitch: '',
          cmZeroEna: '',
          cmZeroTrigger: '',
          cmMotorEna: '',
          cmMotorCurr: '',
          cmMotorClk: '',
          cmMotorForward: ''
        },
        cmshifts:[{
          value: 0,
          label: 'STEP_1'
        }, {
          value: 1,
          label: 'STEP_2'
        }, {
          value: 2,
          label: 'STEP_4'
        }, {
          value: 3,
          label: 'STEP_8'
        }, {
          value: 4,
          label: 'STEP_16'
        }, {
          value: 5,
          label: 'NOCHANGE'
        }],
        disEna:[{
          value: 0,
          label: 'DIS'
        }, {
          value: 1,
          label: 'ENA'
        }, {
          value: 2,
          label: 'NOCHANGE'
        }],
        lowHeight:[{
          value: 0,
          label: 'LOW'
        }, {
          value: 1,
          label: 'HEIGHT'
        }, {
          value: 2,
          label: 'NOCHANGE'
        }],
        noYes:[{
          value: 0,
          label: 'NO'
        }, {
          value: 1,
          label: 'YES'
        }, {
          value: 2,
          label: 'NOCHANGE'
        }],
        fallingRising:[{
          value: 0,
          label: 'FALLING'
        }, {
          value: 1,
          label: 'RISING'
        }, {
          value: 2,
          label: 'NOCHANGE'
        }],
    }
  },
   components: {
    MotorList
    },
  mounted() {
    this.stompClient= this.$store.state.stompClient;
   // this.loadselect();//
   this.motorId=this.$store.state.motorId;
    var _this = this;// ``
    //  Bus.$on('motorIdId',function(val){//firsttxt
    //         _this.motorId=val
    //     })
         Bus.$on('searchInlineopt',function(val){//firsttxt
        var vk= JSON.parse(val)
        if(vk.cmshift>0&vk.cmshift<5){
           _this.formInline.cmshift=vk.cmshift
        }else{
           _this.formInline.cmshift=5
        }
        if(vk.cmstopEna<2){
           _this.formInline.cmstopEna=vk.cmstopEna
        }else{
           _this.formInline.cmstopEna=2
        }
         if(vk.cmemmrEna<2){
           _this.formInline.cmemmrEna=vk.cmemmrEna
        }else{
           _this.formInline.cmemmrEna=2
        }
         if(vk.cmemmrtrigger<2){
            _this.formInline.cmemmrtrigger=vk.cmemmrtrigger
        }else{
           _this.formInline.cmemmrtrigger=2
        }
         if(vk.cmRotate<2){
            _this.formInline.cmRotate=vk.cmRotate
        }else{
           _this.formInline.cmRotate=2
        }
        if(vk.cmZeroSwitch<2){
            _this.formInline.cmZeroSwitch=vk.cmZeroSwitch
        }else{
           _this.formInline.cmZeroSwitch=2
        }
        if(vk.cmZeroEna<2){
            _this.formInline.cmZeroEna=vk.cmZeroEna
        }else{
           _this.formInline.cmZeroEna=2
        }
         if(vk.cmZeroTrigger<2){
           _this.formInline.cmZeroTrigger=vk.cmZeroTrigger
        }else{
           _this.formInline.cmZeroTrigger=2
        }
        if(vk.cmMotorEna<2){
            _this.formInline.cmMotorEna=vk.cmMotorEna
        }else{
           _this.formInline.cmMotorEna=2
        }
        if(vk.cmMotorCurr<2){
           _this.formInline.cmMotorCurr=vk.cmMotorCurr
        }else{
           _this.formInline.cmMotorCurr=2
        }
        if(vk.cmMotorClk<2){
            _this.formInline.cmMotorClk=vk.cmMotorClk
        }else{
           _this.formInline.cmMotorClk=2
        }
        if(vk.cmMotorForward<2){
           _this.formInline.cmMotorForward=vk.cmMotorForward
        }else{
           _this.formInline.cmMotorForward=2
        }
          });
  },
  methods: {
    checkNum:function(val) {
             var reg = /^[+-]?\d*\.?\d{1,3}$/;
              if (!reg.test(val)) {
                return false;
               }
            },
    motorcontrol(ruleForm,direction){//direction 
    if(direction!='reset'&&ruleForm.resetdirection=='seekZero'){
         if(this.checkNum(ruleForm.coord)==false){
           alert("")
                return;
         }
          if(this.checkNum(ruleForm.speed)==false){
              alert("")
                return;
         }
          if(this.checkNum(ruleForm.acce)==false){
              alert("")
                return;
         }
    }
      Bus.$emit('progres',true) 
     this.$store.state.resinfo=""
    
       console.log(direction)
         var messageJson = JSON.stringify({
            coord:ruleForm.coord,speed:ruleForm.speed,acce:ruleForm.acce,motorId:this.$store.state.motorId
          });
         console.log("----allmotor----"+messageJson);
       if(ruleForm.series==true&direction!='quickReset'&direction!='reset'&direction!='seekZero'){
          window.setInterval(()=>{
              inm++;
               console.log(inm+"----direction----"+direction);
               this.stompClient.send('/app/motorcontrol/'+direction,messageJson,{})
               if(this.ruleForm.series)return;
          }, 3000);
       }else{
          console.log("----direction----"+direction);
            this.stompClient.send('/app/motorcontrol/'+direction,messageJson,{})
       }
          var _this = this;// ``
    },
    getCoord(){//
      Bus.$emit('progres',true) 
     this.$store.state.resinfo=""
       this.stompClient.send('/app/getCoord',this.$store.state.motorId,{})
    },
    steplost(){//
      Bus.$emit('progres',true) 
      this.$store.state.resinfo=""
      this.stompClient.send('/app/steplost',this.$store.state.motorId,{})
    },
    motorFlag(){//
      Bus.$emit('progres',true) 
     this.$store.state.resinfo=""
      this.stompClient.send('/app/motorFlag',this.$store.state.motorId,{})
    },
    sensorStop(sensorForm,sensorStop){//
      if(this.checkNum(sensorForm.coord)==false){
              alert("")
                return;
         }
      Bus.$emit('progres',true) 
    this.$store.state.resinfo=""
     var messageJson = JSON.stringify({
            coord:sensorForm.coord,
            sensorId:sensorForm.sensorId,
            motorId:this.$store.state.motorId
          });
        this.stompClient.send('/app/sensorStop/'+sensorStop,messageJson,{})
    },
    grooveMove(grooveForm){//
     if(this.checkNum(grooveForm.coord)==false){
              alert("")
                return;
         }
      Bus.$emit('progres',true) 
     this.$store.state.resinfo=""
     var messageJson = JSON.stringify({
            coord:grooveForm.coord,
            sensorId:grooveForm.sensorId,
            motorId:this.$store.state.motorId
          });
        this.stompClient.send('/app/grooveMove',messageJson,{})
    },
   searchInlineopt(formInline){//
     Bus.$emit('progres',true) 
    this.$store.state.resinfo=""
     var messageJson = JSON.stringify({
           motorId:this.$store.state.motorId
          });
        this.stompClient.send('/app/searchInlineopt',messageJson,{})
    },
    writeInlineopt(formInline){
        Bus.$emit('progres',true) 
    this.$store.state.resinfo=""
     var messageJson = JSON.stringify({
          cmshift: formInline.cmshift,
          cmstopEna: formInline.cmstopEna,
          cmemmrEna: formInline.cmemmrEna,
          cmemmrtrigger: formInline.cmemmrtrigger,
          cmRotate: formInline.cmRotate,
          cmZeroSwitch: formInline.cmZeroSwitch,
          cmZeroEna: formInline.cmZeroEna,
          cmZeroTrigger: formInline.cmZeroTrigger,
          cmMotorEna: formInline.cmMotorEna,
          cmMotorCurr: formInline.cmMotorCurr,
          cmMotorClk: formInline.cmMotorClk,
          cmMotorForward: formInline.cmMotorForward,
           motorId:this.$store.state.motorId
          });
        this.stompClient.send('/app/writeInlineopt',messageJson,{})
    },
    handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
    handleCommand(command) {
        this.$message('click on item ' + command);
      }
  },
  computed: {
    stompinit(){
       return this.$store.state.stompClient
    },
  },
  watch:{
    stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
     'ruleForm.series':{
       handler(newVal, oldVal) {
            console.log(newVal+":"+oldVal);
        },
        deep: true
     },
  },
}
</script>
<style rel="stylesheet/scss" lang="scss">
.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .motorhead{
width:100%;
height:auto;
min-height: 12px;
margin-left: 30px;
margin-top: 5px;
  }
  .motorContent{
width:100%;
height: 740px;
margin-top: 10px;
background: rgb(238, 255, 247);
  }
  .container{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 20px;
       
    }
    .container::before{
        content:attr(title);
        position:absolute;
        left:20%;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
    .el-form-item {
     margin-bottom: 3px;
}
.tdpad{
padding-top: 20px;
}

// .el-form-item{

// .content:disabled{
// margin-left: 0px;
// }
// }
</style>


