    stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
  },
}

</script>
<style rel="stylesheet/scss" lang="scss">
 .containerd{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 20px;
       
    }
    .containerd::before{
        content:attr(title);
        position:absolute;
        left:10%;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
    // .basetable .el-button{
    //    .el-button--primary{
    //       width: 85px;
    //    }
      
    // }
    .basetable td{
 width: 130px;
    }
   
    #nprogress .bar {
  background: red !important; //
  height: 10px;
}
</style>


