<template>
  <div class="" style="background: rgb(238, 255, 247);"> 
      <div style="width:1080px;height:1030px;background: rgb(238, 255, 247);">
         <div class="container" title="" style="width:950px;height:auto; float:left;margin-left:10px">
         <el-radio-group v-model="radio"  style="margin-top:10px">
           <table>
             <tr>
               <td>
                 <el-radio :label="1" class="inradio" title="QCIT.Config.InjectionMax"  style="width: 220px !important;"  border>&emsp;&emsp;&nbsp;&emsp;</el-radio><!-- Qcit.MB.injection -->
               </td>
               <td>
                 <el-radio :label="2" class="inradio" title="QCIT.Config.Injection" style="width: 275px !important;" border></el-radio><!-- Qcit.MB.emergency -->
               </td>
               <td>
                  <el-radio :label="3" class="inradio" title="QCIT.Config.emergency" style="width: 210px !important;" border>&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.intTransport -->
               </td>
               <td>
                 <el-radio :label="4" class="inradio" title="QCIT.Config.InScan" style="width: 224px !important;" border>&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.intTrack -->
               </td>
             </tr>
              <tr>
               <td>
                 <el-radio :label="5" class="inradio" title="QCIT.Config.ScanCode"  style="width: 220px !important;"  border>&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.outTrack -->
               </td>
               <td>
                 <el-radio :label="6" class="inradio" title="QCIT.Config.Transfer" style="width: 275px !important;" border>&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.outTrack -->
               </td>
                <td>
                 <el-radio :label="7" class="inradio" title="QCIT.Config.InBelt" style="width: 210px !important;" border></el-radio><!-- Qcit.MB.outTrack -->
               </td>
               <td>
                 <el-radio :label="8" class="inradio" title="QCIT.Config.BeltToTrack"  style="width: 224px !important;" border></el-radio><!-- Qcit.MB.outTrack -->
               </td>
             </tr>
             <tr>
               <td>
                 <el-radio :label="9" class="inradio" title="QCIT.Config.TrackToBackBelt"  style="width: 220px !important;" border></el-radio><!-- Qcit.MB.outTrack -->
               </td>
               <td>
                 <el-radio :label="10" class="inradio" title="QCIT.Config.TrackToProg" style="width: 275px !important;" border>&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.outTrack -->
               </td>
               <td>
                 <el-radio :label="11" class="inradio" title="QCIT.Config.iProgInit" style="width: 210px !important;" border>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.outTrack -->
               </td>
              <td>
                 <el-radio :label="12" class="inradio" title="QCIT.Config.iProToOne" style="width: 224px !important;" border>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.progressive -->
               </td>
             </tr>
              <tr>
               <td>
                  <el-radio :label="13" class="inradio" title="QCIT.Config.iProStop"  style="width: 220px !important;" border>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.stepbyStep -->
               </td>
                <td>
                 <el-radio :label="14" class="inradio" title="QCIT.Config.iProToBack" style="width: 275px !important;" border>-&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio><!-- Qcit.MB.inrecovery -->
               </td>
                <td>
                  <el-radio :label="15" class="inradio" title="QCIT.Config.backOver" style="width: 210px !important;" border>-&emsp;&emsp;&emsp;</el-radio> <!-- Qcit.MB.recoveryUnload -->
               </td>
                 <td>
                   <el-radio :label="16" class="inradio" title="QCIT.Config.Tubewidth" style="width: 224px !important;" border>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio> <!-- Qcit.MB.recoveryUnload --> 
                   </td>
             </tr>
             <!-- <tr>
               <td>
                  <el-radio :label="17" class="inradio" title="QCIT.Config.initPosition"  style="width: 220px !important;" border>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-radio>
               </td>
             </tr> -->
             <tr>
               <td>
                 <el-input v-model="coord" placeholder=""></el-input>
               </td>
               <td>
                  <el-button type="primary"  @click="deleteCoord()" plain></el-button>
               </td>
                <td>
                  <el-button type="primary" @click="loadCoord(0)" plain></el-button>
               </td>
               <td>
                  <el-button type="primary" @click="loadCoord(1)" plain></el-button>
               </td>
             </tr>
              <tr>
                 <td>
                 <el-button type="primary" @click="readCoord(2)"  plain></el-button>
               </td>
                <td>
                  <el-button type="primary" @click="readCoord(0)" plain></el-button>
               </td>
               <td>
                 <el-button type="primary"  @click="writeCoord(2)" plain></el-button>
               </td>
               <td>
                  <el-button type="primary" @click="writeCoord(0)" plain></el-button>
               </td>
             </tr>
              <tr>
                 <td>
                 <el-button type="primary" @click="readCoord(3)" plain></el-button>
               </td>
               <td>
                  <el-button type="primary" @click="readCoord(1)" plain></el-button>
               </td>
               <td>
                 <el-button type="primary" @click="writeCoord(3)" plain></el-button>
               </td>
               <td>
                  <el-button type="primary" @click="writeCoord(1)" plain></el-button>
               </td>
             </tr>
           </table>
         </el-radio-group>
         <div></div>
         <div></div>
         <div></div>
        <div></div>
         </div>
         <div class="container" title="" style="width:900px;height:auto; float:left;margin-left:10px">
           <div  style="margin-top:10px">
            <el-table
    ref="multipleTable"
    :data="tableData2"
    tooltip-effect="dark"
    style="width: 100%"
     >
 <!-- @selection-change="handleSelectionChange" -->
    <el-table-column
      type="selection"
      width="50" >
    </el-table-column>
    <el-table-column
      label=""
      width="130">
      <template slot-scope="scope">{{ scope.row.motorName }}</template>
    </el-table-column>
    <el-table-column
      prop="forward"
      label="Forward"
      width="80">
             <template slot-scope="scope">
                  <el-input type="text" v-model="scope.row.forward" class="cell-input" :disabled="true"/>
                </template>
    </el-table-column>
     <el-table-column
      prop=""
      label="motorId"
      width="80" v-if="show">
       <template slot-scope="scope">
                  <el-input type="text" v-model="scope.row.motorId" class="cell-input"  />
        </template>
    </el-table-column>
    <el-table-column
      prop="reverse"
      label="Reverse"
      width="80">
       <template slot-scope="scope">
                  <el-input type="text" v-model="scope.row.reverse" class="cell-input"  :disabled="true"/>
                </template>
    </el-table-column>
     <el-table-column
      prop="zero"
      label="zero"
      width="50">
          <template slot-scope="scope">
             <el-checkbox v-model="scope.row.zero" v-bind="{'checked': scope.row.zero? true : false}" @change="v => {update(scope.row, v, 'zero')}"  :disabled="true"></el-checkbox>
         </template>
    </el-table-column>
    <el-table-column
      prop=""
      label=""
      width="80">
             <template slot-scope="scope">
                  <el-input type="text" v-model="scope.row.zeroCoord" class="cell-input"  :disabled="true"/>
                </template>
    </el-table-column>
    <el-table-column
      prop=""
      label=""
      :render-header="renderHeader"
      width="110">
       <template slot-scope="scope">
                  <el-input type="text" v-model="scope.row.coord" class="cell-input"/>
                </template>
    </el-table-column>
    <el-table-column
      prop=""
      label=""
      width="60">
            <template slot-scope="scope">
<el-button type="text" @click="checkDetail(scope.row,0)"></el-button>
</template>
    </el-table-column>
     <el-table-column
      prop=""
      label=""
      width="60" >
            <template slot-scope="scope">
<el-button type="text" @click="checkDetail(scope.row,1)"></el-button>
</template>
    </el-table-column>
     <el-table-column
      prop=""
      label=""
      width="40" >
            <template slot-scope="scope">
<el-button type="text" @click="checkDetail(scope.row,2)">GO</el-button>
</template>
    </el-table-column>
     <el-table-column
      prop=""
      label=""
      width="75">
            <template slot-scope="scope">
<el-button type="text" @click="checkDetail(scope.row,3)">SeekZero</el-button>
</template>
    </el-table-column>
     <el-table-column
      prop=""
      label=""
      width="65" show-overflow-tooltip>
            <template slot-scope="scope">
<el-button type="text" @click="checkDetail(scope.row,4)">Reset</el-button>
</template>
    </el-table-column>
  </el-table>
   </div>
              <!-- <table style="margin-top:10px"  id="tabledata">
                   <tr>
                     <td><el-checkbox v-model="checked" class="parent_check"></el-checkbox></td>
                     <td>Forward</td>
                     <td>Reverse</td>
                     <td>Zero</td>
                     <td><div  style="width:65px"></div></td>
                     <td>
                         <el-row>
                            <el-button type="primary" @click="motorcontrol()" plain></el-button>
                          </el-row>
                      </td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                   </tr>
              </table> -->
         </div>
      </div>
      
</div> 
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import Bus from '../../components/bus' //

export default {
  data() {
    return {
       el: '#example-1',
      list1: [],
      show:false,
      stompClient: null,
      allmotor:[],
      tableData2:[],
      radio:0,
      coord:'',
      coodPoint:'',
      motorId:'',
      motorinfo:[],
      idx:''
    }
  },
  
  mounted() {
     var _this = this
    this.stompClient= this.$store.state.stompClient;
     Bus.$on('allmotor',function(val){//firsttxt
     debugger
            var motorlist=val.split(",")
            _this.allmotor=motorlist
           for(var i=0;i<motorlist.length;i++){
             var obj = {}
             var motor=motorlist[i].split('_')
             obj.motorName=motor[0]
             obj.forward=0
             obj.reverse=0
             obj.zero=false
             obj.zeroCoord=0
             obj.coord=0
             obj.motorId=motor[1]
              console.log('motorId='+obj.motorId)
              if(_this.tableData2.length<9){//
                _this.tableData2.push(obj)
              }
           }
      });
    this.loadselect(this.tableData2);//
    
      Bus.$on('readCoord',function(val){//fi
       debugger
              _this.coord=val
          })
      
      Bus.$on('searchMotor',function(val){//firsttxt
      debugger
           let obj=JSON.parse(val)
            for(let key in obj) {
               let obj1=JSON.parse(obj[key])
              //  console.log('obj1='+obj1)
                var kk=_this.$refs.multipleTable.selection
               for(var i=0;i<kk.length;i++){
                   if(kk[i].motorId==key){
                         for(let key1 in obj1) {
                              if(key1=='forward')kk[i].forward =obj1[key1]
                              if(key1=='reverse')kk[i].reverse=obj1[key1]
                              if(key1=='zeroFlag')kk[i].zero=obj1[key1]
                              if(key1=='zero')kk[i].zeroCoord=obj1[key1]
                              if(key1=='coord')kk[i].coord=obj1[key1]
                         }
                   }
               }
           }
        })
  },
  methods: {
     handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      readCoord(opt){//0  ;1 2 ;3
        Bus.$emit('progres',true) 
        this.$store.state.resinfo=""
       var _this = this
       _this.coord=''
         this.getCoordPoint()
          this.stompClient.send('/app/readCoord/'+opt+'/'+this.motorId+'/'+this.coodPoint)
      },
       writeCoord(opt){//0  ;1; 2 3
       if(this.checkNum(this.coord)==false){
                return;
       }
         Bus.$emit('progres',true) 
        this.$store.state.resinfo=""
       var _this = this;
         this.getCoordPoint()
          this.stompClient.send('/app/writeCoord/'+opt+'/'+this.motorId+'/'+this.coodPoint+'/'+this.coord)
      },
      checkNum:function(val) {
             var reg = /^[+-]?\d*\.?\d{1,3}$/;
              if (!reg.test(val)) {
              alert("")
                return false;
               }
            },
      deleteCoord(){
          Bus.$emit('progres',true) 
         this.$store.state.resinfo=""
         this.getCoordPoint()
         this.stompClient.send('/app/deleteCoord/'+this.coodPoint)
      },
      loadCoord(opt){
          Bus.$emit('progres',true) 
        if(opt==0){
           this.$store.state.resinfo=""
        }else{
          this.$store.state.resinfo=""
        }
         this.stompClient.send('/app/loadCoord/'+opt)
      },
      getCoordPoint(){
         var _this = this;// ``
         if(_this.radio==1){
           this.coodPoint='QCIT.Config.InjectionMax'
           this.motorId=3
         }else if(_this.radio==2){
           this.coodPoint='QCIT.Config.Injection'
           this.motorId=3
         }else  if(_this.radio==16){
           this.coodPoint='QCIT.Config.Tubewidth'
           this.motorId=3
         }else  if(_this.radio==3){
           this.coodPoint='QCIT.Config.emergency'
           this.motorId=0
         }else  if(_this.radio==4){
           this.coodPoint='QCIT.Config.InScan'
           this.motorId=1
         }else if(_this.radio==5){
           this.coodPoint='QCIT.Config.ScanCode'
           this.motorId=1
         }else if(_this.radio==6){
           this.coodPoint='QCIT.Config.Transfer'
           this.motorId=1
         }else if(_this.radio==7){
           this.coodPoint='QCIT.Config.InBelt'
           this.motorId=9
         }else if(_this.radio==8){
           this.coodPoint='QCIT.Config.BeltToTrack'
           this.motorId=9
         }else if(_this.radio==9){
           this.coodPoint='QCIT.Config.TrackToBackBelt'
           this.motorId=8
         }else if(_this.radio==10){
           this.coodPoint='QCIT.Config.TrackToProg'
           this.motorId=4
         }else if(_this.radio==11){
           this.coodPoint='QCIT.Config.iProgInit'
           this.motorId=6
         }else if(_this.radio==12){
           this.coodPoint='QCIT.Config.iProToOne'
           this.motorId=6
         }else if(_this.radio==13){
           this.coodPoint='QCIT.Config.iProStop'
           this.motorId=6
         } else if(_this.radio==14){
           this.coodPoint='QCIT.Config.iProToBack'
           this.motorId=4
         }else if(_this.radio==15){
           this.coodPoint='QCIT.Config.backOver'
           this.motorId=2
         }
        //  else if(_this.radio==17){
        //    this.coodPoint='QCIT.Config.initPosition'
        //    this.motorId=8
        //  }
      },
      loadselect(datatable){
          Bus.$emit('progres',true) 
         this.$store.state.resinfo=""
       console.log('loadallmotor')
        this.stompClient.send('/app/loadMotor')
         
         
     },
     renderHeader(h) {
      return(
         <el-button class="filter-item" type="default" icon="el-icon-search" onClick={()=>this.handleAddItem()}></el-button>
      )
    },
    checkDetail(scopes,opt){
      debugger
      if(opt<3){
         if(this.checkNum(scopes.coord)==false){
                return;
       }
      }
     
        Bus.$emit('progres',true) 
       this.$store.state.resinfo=""
       var motor=scopes.motorId
       var coor=scopes.coord
      this.stompClient.send('/app/motorOpt/'+motor+'/'+coor+'/'+opt)

    },
    waitsleep(delay) {
       var start = (new Date()).getTime();
        while((new Date()).getTime() - start < delay) {
          continue;
       }
    },
    handleAddItem() { 
        Bus.$emit('progres',true) 
      this.$store.state.resinfo=""
      var _this = this;// ``
     var kk= this.$refs.multipleTable.selection
       _this.motorinfo=kk
      var motorArray=new Array();
      //  for(var i=0;i<kk.length;i++){
      //   
      //    this.stompClient.send('/app/searchMotor/'+kk[i].motorId)
      //    _this.idx=i
      //     Bus.$on('searchMotor',(response) => {
      //     
      //       let obj=JSON.parse(response)
      //       for(let key in obj) {
      //          console.log('_this.idx'+_this.idx)
      //                         if(key=='forward')_this.motorinfo[_this.idx].forward =obj[key]
      //                         if(key=='reverse')_this.motorinfo[_this.idx].reverse=obj[key]
      //                         if(key=='zeroFlag')_this.motorinfo[_this.idx].zero=obj[key]
      //                         if(key=="zero")_this.motorinfo[_this.idx].zeroCoord=obj[key]
      //                         if(key=='coord')_this.motorinfo[_this.idx].coord=obj[key]
      //                         kk[_this.idx]=_this.motorinfo[_this.idx]
      //      }
      //       })
      //       waitsleep(1000*1)
      
      
      //  (function(i) {
      //   setTimeout(function() {
      //       console.log(i);
      //   }, (i + 1) * 1000);
      //   })(i)
       
       //   }
       
     for(var i=0;i<kk.length;i++){
        
        motorArray[i]=kk[i].motorId
     }
      this.stompClient.send('/app/searchMotor/'+motorArray)
    }
  },
  computed: {
    stompinit(){
       return this.$store.state.stompClient
    },
  },
  watch:{
    stompinit(newVal,oldVal){
      this.stompClient=newVal
    },
  },
}

</script>
<style rel="stylesheet/scss" lang="scss">
.inradio{
  float:left;
  vertical-align:middle;
   margin-top:0;
}
</style>


