<template>
  <div class="" style="background: rgb(238, 255, 247);"> 
      <div style="width:1080px;height:550px;background: rgb(238, 255, 247);padding-top: 10px;">
      <div class="containerContent" title="" style="width:auto;height:auto;">  
           <table style="margin-top: 20px;">
              <tr>
                  <td colspan="">&emsp;</td>
                  <td><el-button type="primary" @click="start()" plain></el-button></td>
                  <td>&emsp;</td>
                  <td><el-button type="primary" @click="suspend()" plain>{{susp}}</el-button></td>
                  <td colspan="">&emsp;</td>
                  <td colspan=""><el-button type="primary" @click="end()" plain></el-button></td>
                  <td colspan="">&emsp;</td>
                  <td colspan=""><el-button type="primary" @click="logclear()" plain></el-button></td>
                  <td colspan="">&emsp;<span>: {{ count }}</span></td>
             <tr>
              <tr>
               <td colspan="8" rowspan="20">
                 <el-input type="textarea" style="width:500px;height:450px;" id="desces" v-model="desc" :rows="20"  readonly></el-input>
               </td>
             </tr>
             
         </table>
      </div>
      </div> 
</div> 
</template>
<script>
import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import Bus from '../../components/bus' //
import VueWorker from 'vue-worker'

export default {
  data() {
    return {
      list1: [],
      stompClient: null,
      desc:'',
      worker: null,
      susp:'',
      nowtime:'',
      count:0,
      readonly: true,
    }
  },
  
  mounted() {
  //  this.testconnection()
  this.stompClient= this.$store.state.stompClient
   var _this=this
   
   Bus.$on('testMoveone',function(val){//firsttxt
                   _this.desc=_this.desc+"\n"+val
                    _this.getNowTime()
                    _this.desc=_this.desc+"\n"+''+ _this.nowtime+"\n"
                    _this.count=_this.count+1
                    
               })
                Bus.$on('scanBarcode',function(val){//firsttxt
                    _this.desc=_this.desc+"\n"+val
                })
    // $('#desces').removeAttr("disabled")
  },
  methods: {
    testconnection(){

    },
    getNowTime(){
      this.nowtime= new Date().getFullYear() +"-" +(new Date().getMonth() + 1) + "-" +new Date().getDate() 
          +" " + new Date().getHours() +":" +new Date().getMinutes() +":" + new Date().getSeconds();
    },

      start(){
          
          var _this=this
           _this.fthis='',
          this.susp=''
         this.getNowTime()
        // this.desc=''
          if(this.desc==''){
            _this.desc=''+_this.nowtime+"\n";
          }else{
             _this.desc=this.desc+"\n"+''+_this.nowtime+"\n";
          }
          // _this.stompClient.send('/app/testMoveone')
          new Promise(function (resolve,reject) {
             // resolve()
              _this.stompClient.send('/app/testMoveone')
              // resolve(resData);
            //    resolve();
         }).then(function () {
            //  _this.desc=_this.desc+"\n"+''+_this.nowtime+"\n";
            //   console.log('!');
           // resolve();
         });
         
      },
      suspend(){
        var _this=this
        var suspstate=''
         new Promise(function (resolve,reject) {
            if(_this.susp==''){
              _this.susp=''
              suspstate=0
            }else{
             _this.susp=''
             suspstate=1
            }
              _this.stompClient.send('/app/testMoveStop/'+suspstate)
          })
      },
      end(){
         var _this=this
        this.susp=''
        var suspstate=2
         new Promise(function (resolve,reject) {
              _this.stompClient.send('/app/testMoveStop/'+suspstate)
          })
          //  _this.getNowTime()
          //  _this.desc=_this.desc+"\n"+''+ _this.nowtime+"\n";
      },
      logclear(){
         this.desc=''
      }
  },

  computed: {
    stompinit(){
       return this.$store.state.stompClient
    }
  },
  watch:{
    stompinit(newVal,oldVal){
      this.stompClient=newVal
    }
  },
}

</script>
<style rel="stylesheet/scss" lang="scss">
 .motorContent{
width:100%;
height:auto;
margin-top: 10px;

  }
    .container{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 20px;
       
    }
    .container::before{
        content:attr(title);
        position:absolute;
        left:100px;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
        .containerContent{
        position:relative;
        border:1px solid #DCDFE6;
        margin-top: 10px;
       
    }
    .containerContent::before{
        content:attr(title);
        position:absolute;
        left:100px;
        transform:translateX(-50%);
        -webkit-transform:translate(-50%,-50%);
        padding:0 10px;
        background-color:#fff;
    }
</style>


