// vue  Vuex
import Vue from 'vue';
import Vuex from 'vuex';
// vueVuex
Vue.use(Vuex);
//  store module
const store = new Vuex.Store({
  state: { counter:1,
          stompClient:null,//
          motorId:0,
          ip:null,
          initInfo:'',
          rootPage:0,
          firstPage:0,
          secondPage:0,
          thirdPage:0,
          connectstate:false,
          uid:'',
          resinfo:'',//
          },
});
export default store


